/* ============================================ *
 * Form-components - Variables
 * ============================================ */

$checkbox__icon: '✓';

/* ============================================ *
 * Form-components - COMMON
 * ============================================ */

//checkbox

.checkbox-wrapper {
    > .checkbox {
        position: relative;
        visibility: hidden;
        color: $c-white;
        width: 16px;
        height: 16px;
    }

    .checkbox::after {
        content: '';
        display: block;
        @include centred-content(true,true);
        font-size: 20px;
    }

    .checkbox::before {
        content: '';
        display: block;
        border: 1px solid $c-white;
        width: 100%;
        height: 100%;
        visibility: visible;
    }

    > .checkbox:checked::after {
        content: $checkbox__icon;
        visibility: visible;
    }
}
